@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.finder{width:20%}.finder__input{display:flex;align-items:center;height:30px;border-radius:10px;text-align:center;font-family:"Special Elite", cursive}

.header{width:100%;max-height:150px;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:10px 30px;background-color:aquamarine}@media (min-width: 760px){.header{flex-direction:row;justify-content:space-between}}.header__info{display:flex;flex-direction:column;justify-content:center;align-items:center;width:70%}@media (min-width: 760px){.header__info{flex-direction:row;justify-content:flex-start}}.header__logo{width:40px;margin-bottom:10px}@media (min-width: 760px){.header__logo{margin-bottom:0;margin-right:10px}}.header__title{font-family:"Special Elite", cursive;text-align:center;font-size:16px;margin-bottom:10px;border-right:solid 3px black;white-space:nowrap;overflow:hidden;animation:animated-text 4s steps(29, end) 1s 1 normal both,animated-cursor 900ms steps(29, end) infinite}@keyframes animated-text{from{width:0}to{width:190px}}@keyframes animated-cursor{from{border-right-color:black}to{border-right-color:transparent}}@media (min-width: 760px){.header__title{margin-bottom:0;text-align:left}}.header__title span{font-size:20px}

.card-container{display:flex;flex-direction:column;align-items:center;padding:20px;min-height:300px;max-width:300px;border:3px solid aquamarine;border-radius:10px}.card-container.fav{background-color:aquamarine;border:3px solid lightgrey;border-radius:10px;box-shadow:0px 0px 60px;animation:infinite borderAnimation 2s}@keyframes borderAnimation{0%,100%{box-shadow:0px 0px 20px}50%{box-shadow:0px 0px 0px}}.card-container.fav button{background-color:lightgrey}.card-container__title{margin-top:10px}.card-container__img{width:100%}.card-container__button{margin-top:40px;cursor:pointer;padding:5px 10px;border-radius:5px;background-color:aquamarine}.card-container__button--heart{display:inline-block;width:20px;color:red}

.list{display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:10px}.list-container{display:grid;grid-template-columns:1fr;grid-gap:15px;gap:15px;max-width:500px;margin:20px auto}@media (min-width: 760px){.list-container{grid-template-columns:1fr 1fr;max-width:750px}}@media (min-width: 1200px){.list-container{grid-template-columns:1fr 1fr 1fr}}.no-list-container{display:flex;justify-content:center;align-items:center;min-height:500px}.button-close{padding:8px 15px;background-color:aquamarine;border-radius:10px;cursor:pointer;font-weight:700;text-transform:uppercase}.button-close span{font-size:20px;font-weight:700;margin-left:10px}

.initial-ideas{height:80vh;padding:50px;display:flex;justify-content:center;align-items:center}.initial-ideas__options{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:20px 10px;border:2px solid aquamarine;border-radius:10px;box-shadow:0px 0px 20px;animation:infinite borderAnimation 2s}@media (min-width: 760px){.initial-ideas__options{padding:40px 60px}}@keyframes borderAnimation{0%,100%{box-shadow:0px 0px 20px}50%{box-shadow:0px 0px 0px}}.initial-ideas__options--text{font-family:"Special Elite", cursive;margin-bottom:20px;display:block;font-size:30px;text-align:center}.initial-ideas__options--text span{margin-top:20px;display:block;font-size:24px}.initial-ideas__options--recommendations{display:flex;flex-direction:column;align-items:center;font-family:"Special Elite", cursive;font-size:20px;margin-top:40px;width:100%}@media (min-width: 760px){.initial-ideas__options--recommendations{flex-direction:row;justify-content:center}}.initial-ideas__options--recommendations input{margin-right:15px;margin-bottom:20px;cursor:pointer}.initial-ideas__options--recommendations label{text-transform:capitalize;margin-right:15px}

:root{--background-color: #e1ad01;--card-color: orange}*{margin:0;padding:0;list-style:none;text-decoration:none;border:none;outline:none;box-sizing:border-box;max-width:100vw}

